<template>
    <div class="tstuwork">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-c-scale-to-original"></i> 课程设计
                </el-breadcrumb-item>
                <el-breadcrumb-item v-if="items">{{items.teaching.name}}</el-breadcrumb-item>
            </el-breadcrumb> 
        </div>
        <div class="container"  v-loading="loading">
            <div class="form">
                <h3 v-if="items">{{items.teaching.name}}</h3>
                <el-form v-if="items" ref="form" label-width="90px">
                    <el-form-item class="work-date" size="small" label="指导老师：">
                        {{items.teaching.teacherName}}
                    </el-form-item>
                    <el-form-item class="work-date" size="small" label="学生：">
                        {{items.studentName}}
                    </el-form-item>
                    <el-form-item class="work-date" size="small" label="作业项目：">
                        {{items.workProjectInfoDTO.name}}
                    </el-form-item>
                    <el-form-item label="项目简介：">
                        <div class="teacherishi">{{items.workProjectInfoDTO.desc}}</div>
                    </el-form-item>
                    <el-form-item label="相关资料：">
                        <template v-for="item in items.workProjectInfoDTO.filelist">
                        <a target="_blank" :href="downurls+item.url" class="download-bg"  >
                          <el-button class="download" type="text">{{item.name}}</el-button>
                          <i class="el-icon-download"></i>
                        </a><br />
                        </template>
                    </el-form-item>
                    <el-form-item label="工艺卡片：">
                        <el-form v-if="status==1||status==3" v-for="(item,index) in craftCardInfoDTOList" ref="form" label-width="85px" :key="index"  class="form-in">
                          <h4>工序{{item.sort+1}}</h4>
                          <el-form-item class="work-date" size="small" label="加工表面：">
                              {{item.surfaceName}}
                          </el-form-item>
                          <el-form-item class="work-date" size="small" label="加工形式：">
                              {{item.processingMethodName}}
                          </el-form-item>
                          <el-form-item class="work-date" size="small" label="工序内容：">
                              <div class="teacherishi">{{item.processContent}}</div>
                          </el-form-item>
                        </el-form>

                        <el-form v-if="status==0||status==2"  ref="form" v-for="(item, index) in craftCardInfoDTOList" :key="index" label-width="85px" class="form-in">
                          <h4>工序{{index+1}}   <el-button :disabled="craftCardInfoDTOList.length<2"  class="del-button" size="mini" @click="delcarts(index)" type="danger" plain>删除卡片</el-button></h4>
                          <el-form-item class="work-date" size="small" label="加工表面：">
                              <input type="text"  v-model="item.surfaceName" placeholder="请输入加工表面"/>
                          </el-form-item>
                          <el-form-item class="work-date" size="small" label="加工形式：">
                            <input type="text"  v-model="item.processingMethodName" placeholder="请输入加工形式"/>
                          </el-form-item>
                          <el-form-item class="work-date work-date-cont" size="small" label="工序内容：">
                              <div class="modify-bg">
                                <div class="modify-top">
                                    <el-input
                                      type="textarea"
                                      placeholder="请输入内容"
                                      v-model="item.processContent"
                                      maxlength="300"
                                      :autosize="{ minRows: 3}"
                                      show-word-limit
                                    ></el-input>
                                </div>
                                <div class="modify">
                                  <p><el-button type="primary" :disabled="index==0" size="mini" plain @click="removefront(index)"> 向前移动 </el-button></p>
                                  <p><el-button :disabled="index+1==craftCardInfoDTOList.length" type="primary" size="mini" plain @click="removeafter(index)"> 向后移动 </el-button></p>
                                  <p><el-button type="primary" size="mini" plain @click="addafter(index)"> 后方插入 </el-button></p>
                                </div>
                              </div>
                          </el-form-item>
                        </el-form>
                    </el-form-item>
                    <el-form-item label="作业附件：">
                        <template v-for="(item,index) in items.workFileDTOList">
                          <a target="_blank" :href="downurls+item.url" class="download-bg"  >
                            <el-button class="download" type="text">{{item.name}}</el-button>
                          </a><i  v-if="status==0||status==2" class="el-icon-delete" @click="delworkFileDTO(item.id,index)" style="margin-left:20px;color:red;cursor:pointer;"></i><br />
                        </template>
                        <a class="file"  v-if="status==0||status==2">上传附件<input class="upload" @change='add_img' :value="filevalue" type="file"></a>
                    </el-form-item>
                    
                    <el-form-item label="作业讨论：">
                        <el-tabs type="border-card">
                          <el-tab-pane label="私信老师">
                            <talkCmp @getmore="getmore" @pus="Addmessagebystudent" @dele="deletemessage" :workMessageInfoDTOList="workMessageInfoDTOList" :total="totalsi"/>
                          </el-tab-pane>
                          <el-tab-pane label="公共讨论区">
                            <talkCmp @getmore="getmore2" @pus="Addmessagebystudent2" @dele="deletemessage2" :workMessageInfoDTOList="workMessageInfoDTOList2" :total="totalsi2"/>
                          </el-tab-pane>
                        </el-tabs>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <div class="savebg" v-if="items">
          <div class="savein">
            <template v-if="status==0||status==2">
              <p class="fl"><el-button size="small" type="primary" @click="savework(0)">保存</el-button> {{tishi}}</p>
              <p class="fr"><el-button size="small" @click="submitwork" type="primary">保存并提交</el-button> 请于{{items.teaching.deadlineTime}} 之前提交作业</p>
            </template>
            <template v-if="status==1">
              <p class="fr">
                <el-alert
                  title="作业已提交"
                  :closable="false"
                  effect="dark"
                  type="success">
                </el-alert>
              </p>
            </template>
            <template v-if="status==3">
              <p class="fr">
                <el-alert
                  title="作业已完成"
                  :closable="false"
                  effect="dark"
                  type="success">
                </el-alert>
              </p>
            </template>
          </div>
        </div>
    </div>
</template>

<script>
import {fetchGetstudentworkinfobyid, fetchGetallprocessingmethod, fetchGetallsurface, fetchAddcraftcardlist, fetchAddmessagebystudent, fetchDeletestudentmessage, fetchSubmit, fetchStudentlistmessage, fetchDeletewrokfile, fetchtDeletestudentmessage, fetchAddstudentmessage, fetchStudentlistbyteachindid} from '../../axios/requests'
import {post3, downurl} from '../../axios/index'
import talkCmp from '../common/talkCmp.vue'
export default {
    name: 'course',
    components:{talkCmp},
    data() {
        return {
          tishi:'保存成功',
          status:0,
          loading:false,
          id:'',
          items:'',
          craftCardInfoDTOList:[],//卡片数组
          workMessageInfoDTOList:[],//评论数组
          totalsi:0,
          itemsbm:[],
          itemsxs:[],

          talktext:'',
          timer:'',
          filevalue:'',
          downurls:'',
          startId:0,

          //讨论消息
          startId2:0,
          workMessageInfoDTOList2:[],
          totalsi2:0,
          teachingid:''
        };
    },
    mounted(){
      this.downurls = downurl;
      let {id} = this.$route.params;
      this.id=id;
      this.Getworkinfobyid();
      this.Studentlistmessage(1)
       
    },
    methods: {

      async Addmessagebystudent2(talktext) {//添加讨论消息
        let {teachingid} = this;
        if (!talktext) {
          this.$message({
            showClose: true,
            message: '讨论内容不能为空',
            type: 'error'
          });
          return;
        };
        this.loading = true;
        try {
          let response = await fetchAddstudentmessage(teachingid, talktext);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '发表成功',
              type: 'success'
            });
            this.talktext = '';
            this.Studentlistmessage2(1)
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      getmore2(pagenum){
        this.Studentlistmessage2(pagenum)
      },
      async Studentlistmessage2(pageNum) {//得到讨论消息
        var size=10;
        if (pageNum==1) {
          this.workMessageInfoDTOList2=[];
          this.startId=0;
          var size = 5;
        };
        try {
          let response = await fetchStudentlistbyteachindid(this.items.teaching.id, pageNum, size, this.startId2);
          if (response.status.code ==0) {
            this.totalsi2 = response.result.teachingMessageInfoDTOBaseListDTO.total;
             this.workMessageInfoDTOList2=response.result.teachingMessageInfoDTOBaseListDTO.list.reverse().concat(this.workMessageInfoDTOList2)
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
        }
      },
      deletemessage2(e){
        this.$confirm('确定要删除吗？', '提示', {
              type: 'warning'
          })
          .then(() => {
              this.Deletestudentmessage2(e.id, e.index);
          })
          .catch(() => {});
      },
      async Deletestudentmessage2(id, index) {//删除讨论消息
        this.loading = true;
        try {
          let response = await fetchtDeletestudentmessage(id);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            });
            this.workMessageInfoDTOList2.splice(index, 1);
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },

      delworkFileDTO(id,index){
        this.$confirm('确定要删除作业附件吗？', '提示', {
              type: 'warning'
          })
          .then(() => {
              this.Deletewrokfile(id,index);
          })
          .catch(() => {});
      },
      async Deletewrokfile(id, index) {//删除作业附件
        this.loading = true;
        try {
          let response = await fetchDeletewrokfile(id);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            });
            this.items.workFileDTOList.splice(index, 1);
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async add_img(event){ //学生附件上传
          const loading = this.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading'
          });
          let reader =new FileReader();
          let img1=event.target.files[0];
          let form = new FormData(); 
          form.append('file',img1,img1.name);
          form.append('workId',this.id);
          try {
            let response = await post3('/workfile/addworkfile/',form);
            if (response.status.code ==0) {
              this.pageNum = 1;
              this.$message({
                message: '上传成功',
                type: 'success'
              });
              this.Getworkinfobyid();
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) { 
            console.log(e);
          } finally {
            loading.close()
            this.filevalue = '';
          } 
        },
      timers() {//定时保存
　　　　this.timer = setInterval( () => {
          this.savework(2)
　　　　}, 10000);
      },
      addafter(index){//向后添加
        let data = {'surfaceName':'','processingMethodName':'','processContent':'','id':'','sort':''};
        this.craftCardInfoDTOList.splice(index+1, 0, data)
      },
      delcarts(index){//删除卡片
        this.craftCardInfoDTOList.splice(index, 1)
      },
      removefront(index){//向前移动
        let data = this.craftCardInfoDTOList[index];
        this.craftCardInfoDTOList.splice(index, 1);
        this.craftCardInfoDTOList.splice(index-1, 0, data)
      },
      removeafter(index){//向后移动
        let data = this.craftCardInfoDTOList[index];
        this.craftCardInfoDTOList.splice(index, 1);

        this.craftCardInfoDTOList.splice(index+1, 0, data)
      },
      viewwork(){
        this.$router.push({path:'workdetail'});
      },
      // 保存并提交
      submitwork() {
          var goif = false;
        this.craftCardInfoDTOList.forEach((item, index) =>{
          if (!item.surfaceName) {
            this.$message({
              showClose: true,
              message: '请为工序'+parseInt(index+1)+'填写加工表面',
              type: 'warning'
            });
            goif = true;
            return;
          }
          if (!item.processingMethodName) {
            this.$message({
              showClose: true,
              message: '请为工序'+parseInt(index+1)+'填写加工形式',
              type: 'warning'
            });
            goif = true;
            return;
          };
          if (!item.processContent) {
            this.$message({
              showClose: true,
              message: '请为工序'+parseInt(index+1)+'填写工序内容',
              type: 'warning'
            });
            goif = true;
            return;
          }
        })
        if (goif) {
          return;
        };
          this.$confirm('确定提交？提交完成不能修改', '提示', {
              type: 'warning'
          })
          .then(() => {
              this.savework(1);
          })
          .catch(() => {});
      },
      deletemessage(e){
        this.$confirm('确定要删除吗？', '提示', {
              type: 'warning'
          })
          .then(() => {
              this.Deletestudentmessage(e.id, e.index);
          })
          .catch(() => {});
      },
      async Deletestudentmessage(id, index) {//删除消息
        this.loading = true;
        try {
          let response = await fetchDeletestudentmessage(id);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            });
            this.workMessageInfoDTOList.splice(index, 1);
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Submits(){//提交作业
        this.loading = true;
        try {
          let response = await fetchSubmit(this.id);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '作业提交成功',
              type: 'success'
            });
            this.Getworkinfobyid()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async savework(type){//保存作业
        var list = this.craftCardInfoDTOList.map((r, index)=>{
          var ite = {
            'craftCardId':r.id,
            'surface':r.surfaceName,
            'processingMethod':r.processingMethodName,
            'processContent':r.processContent,
            'sort':index
          }
          return ite;
        })
        if (type!=2) {
          this.loading = true;
        }else{
          this.tishi = "正在自动保存...";
        }
        var that = this;
        try {
          let response = await fetchAddcraftcardlist(this.id, list);
          if (response.status.code ==0) {
            if (type==1) {
              this.Submits()
            }else if(type==0){
              this.$message({
                showClose: true,
                message: '作业保存成功',
                type: 'success'
              });
            }else{
              setTimeout(function (){that.tishi = "保存成功"},1500);
            }
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Getworkinfobyid() {//得到作业详情
        this.loading = true;
        let {id} = this;
        try {
          let response = await fetchGetstudentworkinfobyid(id);
          if (response.status.code ==0) {
            this.items = response.result;
            this.teachingid = response.result.teaching.id;
            this.Studentlistmessage2(1);
            // this.workMessageInfoDTOList = response.result.workMessageInfoDTOList;
            this.status = response.result.status;
            if (response.result.status==0||response.result.status==2) {
              this.timers();
            };
            if (response.result.craftCardInfoDTOList.length==0) {
              this.craftCardInfoDTOList = [{
                'surfaceName':'',
                'processingMethodName':'',
                'processContent':'',
                'id':'',
                'sort':''
              }]
            }else{
              this.craftCardInfoDTOList = this.$sortBykey(response.result.craftCardInfoDTOList,'sort');
            }
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Addmessagebystudent(talktext) {//添加作业消息
        let {id} = this;
        if (!talktext) {
          this.$message({
            showClose: true,
            message: '讨论内容不能为空',
            type: 'error'
          });
          return;
        };
        this.loading = true;
        try {
          let response = await fetchAddmessagebystudent(id, talktext);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '发表成功',
              type: 'success'
            });
            this.talktext = '';
            this.Studentlistmessage(1)
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      getmore(pagenum){
        this.Studentlistmessage(pagenum)
      },
      async Studentlistmessage(pageNum) {//得到私信消息
        var size=10;
        if (pageNum==1) {
          this.workMessageInfoDTOList=[];
          this.startId=0;
          var size=5;
        };
        try {
          let response = await fetchStudentlistmessage(this.id, pageNum, size, this.startId);
          if (response.status.code ==0) {
            this.totalsi = response.result.total;
             this.workMessageInfoDTOList=response.result.list.reverse().concat(this.workMessageInfoDTOList)
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
        }
      },
      // async Getallprocessingmethod() {//得到加工形式
      //   try {
      //     let response = await fetchGetallprocessingmethod();
      //     if (response.status.code ==0) {
      //       this.itemsxs = response.result;
      //     }else{
      //       this.$message.error(response.status.description);
      //     }
      //   } catch (e) {
      //     console.log(e);
      //   } finally {
      //   }
      // },
    }
};
</script>

<style lang='scss'>
  .form-in{
    line-height:28px;
    h4{
      padding-left:4px;
    }
    .el-form-item__label,.el-form-item__content{
      line-height:36px;
    }
  }
  .tstuwork .el-tabs__content{
    padding:0;
  }
</style>

<style lang='scss' scoped>
    .tstuwork{
      // position: relative;
      .crumbs{
          padding-left:20px;
        }
        .savebg{
          background:#eee;
          position:fixed;
          z-index:99999999;
          bottom:0;
          height:47px;
          padding:0 28px 0 11px;
          left:0;
          box-sizing: border-box;
          width:100%;
          .savein{
            padding:10px 20px;
            box-sizing: border-box;
            width:100%;
            height:47px;
            color:#888;
            font-size:14px;
          }
        }
        .modify-bg{
          display:flex;
          .modify-top{
            flex:1;
            min-width:0;
          }
          .modify{
            width:100px;
            margin-top:-37px;
            padding-left:20px;
          }
        }
        h4 .del-button{
          float:right;
          margin-right:20px;
          margin-top:10px;
        }
        .form{
          color:#444;
          .work-date{
            margin-bottom:0;
          }
          h3{
            font-size:16px;
            margin-bottom:15px;
          }
          .teacherishi{
            line-height:22px;
            padding-top:9px;
          }
          .download-bg{
            color:#409EFF;
            .download{
              padding:0px;
            }
          }
          .form-in{
            border:1px solid #ccc;
            padding:5px 15px 10px;
            background:#f9f9f9;
            margin-top:10px;
            .teacherishi{
              padding-top:5px;
            }
          }
          .talk-bg{
            margin-top:15px;
            
            .talk{
              overflow:hidden;
              line-height:24px;
            }
            div.pic{
              width:24px;
              height:24px;
              background:#ccc;
              border-radius:24px;
              margin-right:6px;
            }
            div.pic img{
              display:block;
              width:24px;
              height:24px;
              border-radius:24px;
            }
            p.fl{
              color:#666;
              font-size:12px;
            }
            .text{
              background:#ffeeee;
              border:1px solid #ffb8b8;
              border-radius:5px;
              width:60%;
              padding:10px 15px;
              margin-left:30px;
              line-height:22px;
              box-sizing:border-box;
            }
            .green{
              background:#f2ffeb;
              border:1px solid #a3e881;
            }
            .self{
              padding-right:30px;
              position: relative;
            }
            .deldiscuss{
              position:absolute;
              right:4px;
              top:4px;
              font-size:16px;
              cursor:pointer;
            }
          }
          .push-talk{
            width:62%;
            margin-left:30px;
            margin-top:30px;
            border:1px solid #ccc;
            border-radius:6px;
          }
          .push-button{
            padding-left:30px;
            margin-top:10px;
          }
        }
        .stu-list{
            border:1px solid #ccc;
        }
    }
    .file {
        float:left;
        margin-bottom:10px;
        cursor:pointer;
        position: relative;
        display: inline-block;
        background: #D0EEFF;
        border: 1px solid #99D3F5;
        border-radius: 4px;
        padding: 4px 12px;
        overflow: hidden;
        color: #1E88C7;
        text-decoration: none;
        text-indent: 0;
        line-height: 20px;
    }
    .file input {
        position: absolute;
        font-size: 100px;
        right: 0;
        top: 0;
        opacity: 0;
        cursor:pointer;
    }
    .file:hover {
        background: #AADFFD;
        border-color: #78C3F3;
        color: #004974;
        text-decoration: none;
    }
